'use client'

import React, { useEffect, useRef, useState } from 'react'
import { motion, AnimatePresence } from 'motion/react'
import { Send, X, MessageSquare, Loader, Sparkle, User, UserCheck } from 'lucide-react'
import { getSocket } from '@/lib/socket'
import axios from 'axios'

interface IMessage {
    _id?: string
    roomId: string
    text: string
    senderId: string
    time: string
    createdAt?: Date
}

export default function NegotiationChat({
    productId,
    buyerId,
    farmerId,
    farmerName,
    currentUserRole = "buyer",
    onClose
}: {
    productId: string,
    buyerId: string,
    farmerId: string,
    farmerName: string,
    currentUserRole?: "buyer" | "farmer",
    onClose: () => void
}) {
    const currentUserId = currentUserRole === "buyer" ? buyerId : farmerId;
    const [messages, setMessages] = useState<IMessage[]>([])
    const [newMessage, setNewMessage] = useState("")
    const [loading, setLoading] = useState(false)
    const [suggestions, setSuggestions] = useState<string[]>([])
    const chatBoxRef = useRef<HTMLDivElement>(null)
    const roomId = `negotiation:${farmerId}:${buyerId}`

    useEffect(() => {
        const socket = getSocket()
        socket.emit("join-room", roomId)

        const handleNewMessage = (msg: IMessage) => {
            if (msg.roomId === roomId) {
                setMessages((prev) => [...prev, msg])
            }
        }

        socket.on("send-message", handleNewMessage)

        const fetchMessages = async () => {
            try {
                const res = await axios.post('/api/chat/messages', { roomId })
                setMessages(res.data)
            } catch (error) {
                console.error("Error fetching messages", error)
            }
        }

        fetchMessages()

        return () => {
            socket.off("send-message", handleNewMessage)
        }
    }, [roomId])

    useEffect(() => {
        chatBoxRef.current?.scrollTo({
            top: chatBoxRef.current.scrollHeight,
            behavior: "smooth"
        })
    }, [messages])

    const sendMsg = () => {
        if (!newMessage.trim()) return

        const socket = getSocket()
        const message = {
            roomId,
            text: newMessage,
            senderId: currentUserId,
            time: new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" })
        }

        socket.emit("send-message", message)
        setNewMessage("")
    }

    const getAiSuggestions = async () => {
        setLoading(true)
        try {
            const lastMsg = messages.filter(m => m.senderId !== currentUserId).at(-1)
            const res = await axios.post("/api/chat/ai-suggestions", {
                message: lastMsg?.text || "Started a new negotiation for bulk ordering.",
                role: currentUserRole
            })
            setSuggestions(res.data)
        } catch (error) {
            console.error("AI Suggestions error", error)
        } finally {
            setLoading(false)
        }
    }

    return (
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-[300] flex items-center justify-center p-4 bg-black/60 backdrop-blur-md"
            onClick={onClose}
        >
            <motion.div
                initial={{ scale: 0.9, y: 20 }}
                animate={{ scale: 1, y: 0 }}
                exit={{ scale: 0.9, y: 20 }}
                className="bg-white w-full max-w-lg rounded-[3rem] shadow-3xl overflow-hidden flex flex-col h-[600px] max-h-full"
                onClick={(e) => e.stopPropagation()}
            >
                {/* Header */}
                <div className="bg-zinc-900 p-8 text-white flex items-center justify-between">
                    <div className="flex items-center gap-4">
                        <div className="w-12 h-12 bg-white/10 rounded-2xl flex items-center justify-center">
                            <UserCheck className="text-green-400" size={24} />
                        </div>
                        <div>
                            <h3 className="text-lg font-black tracking-tight">{farmerName}</h3>
                            <div className="flex items-center gap-2">
                                <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
                                <span className="text-[10px] font-black uppercase tracking-widest text-zinc-400">Direct Negotiation</span>
                            </div>
                        </div>
                    </div>
                    <button onClick={onClose} className="p-2 hover:bg-white/10 rounded-xl transition-colors">
                        <X size={20} />
                    </button>
                </div>

                {/* Chat Area */}
                <div className="flex-1 overflow-y-auto p-8 space-y-4 bg-zinc-50/50" ref={chatBoxRef}>
                    {messages.length === 0 && (
                        <div className="text-center py-10 space-y-4">
                            <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center mx-auto shadow-sm">
                                <MessageSquare className="text-zinc-200" size={32} />
                            </div>
                            <p className="text-zinc-400 text-xs font-bold uppercase tracking-widest">Start negotiating for bulk rates</p>
                        </div>
                    )}

                    {messages.map((msg, i) => (
                        <motion.div
                            key={i}
                            initial={{ opacity: 0, x: msg.senderId === currentUserId ? 20 : -20 }}
                            animate={{ opacity: 1, x: 0 }}
                            className={`flex ${msg.senderId === currentUserId ? "justify-end" : "justify-start"}`}
                        >
                            <div className={`max-w-[80%] p-4 rounded-2xl shadow-sm ${msg.senderId === currentUserId
                                ? "bg-zinc-900 text-white rounded-br-none"
                                : "bg-white text-zinc-800 border border-zinc-100 rounded-bl-none"
                                }`}>
                                <p className="text-sm font-medium leading-relaxed">{msg.text}</p>
                                <span className="text-[9px] opacity-50 block mt-2 text-right font-black uppercase">{msg.time}</span>
                            </div>
                        </motion.div>
                    ))}
                </div>

                {/* Suggestions */}
                <div className="px-8 pb-4 bg-zinc-50/50">
                    <div className="flex items-center justify-between mb-3">
                        <span className="text-[9px] font-black uppercase tracking-widest text-zinc-400">AI Negotiation Expert</span>
                        <motion.button
                            whileTap={{ scale: 0.95 }}
                            onClick={getAiSuggestions}
                            disabled={loading}
                            className="text-[9px] font-black bg-green-100 text-green-700 px-3 py-1 rounded-full flex items-center gap-1 border border-green-200"
                        >
                            <Sparkle size={10} />
                            {loading ? "Thinking..." : "Get Advise"}
                        </motion.button>
                    </div>
                    <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide">
                        {suggestions.map((s, i) => (
                            <button
                                key={i}
                                onClick={() => setNewMessage(s)}
                                className="whitespace-nowrap px-4 py-2 bg-white border border-zinc-200 rounded-xl text-xs font-bold text-zinc-600 hover:border-green-500 hover:text-green-600 transition-all shadow-sm"
                            >
                                {s}
                            </button>
                        ))}
                    </div>
                </div>

                {/* Input Area */}
                <div className="p-8 bg-white border-t border-zinc-100">
                    <div className="flex gap-4">
                        <input
                            type="text"
                            placeholder="Propose a rate or ask for bulk info..."
                            className="flex-1 bg-zinc-50 px-6 py-4 rounded-2xl outline-none focus:ring-2 focus:ring-green-500/20 focus:border-green-500 border border-zinc-100 font-bold text-sm transition-all"
                            value={newMessage}
                            onChange={(e) => setNewMessage(e.target.value)}
                            onKeyDown={(e) => e.key === 'Enter' && sendMsg()}
                        />
                        <motion.button
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                            onClick={sendMsg}
                            className="bg-green-600 text-white p-4 rounded-2xl shadow-xl shadow-green-600/20 hover:bg-green-700 transition-colors"
                        >
                            <Send size={20} />
                        </motion.button>
                    </div>
                </div>
            </motion.div>
        </motion.div>
    )
}
